@import './summary-side';
@import './summary-details';

.summary {
    display: flex;
    overflow-y: auto;
    overflow-x: hidden;

    &__main {
        flex: 1;
    }

    &__wrapper {
        padding: 2rem;
        box-shadow: 0 10px 43px -2px rgba($theme, 0.15);
        border-radius: 15px;
        margin-bottom: 2rem;
    }

    &__buttons {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        width: 100%;
        margin-bottom: 2rem;

        .button{
            font-weight: normal;

            &:first-child {
                margin-right: auto;
            }

            &:last-child {
                margin-left: 1rem;
            }

            &:not(.export) {
                img:not(.export){
                    border: 1px solid;
                    border-radius: 50%;
                    width: 1.5rem;
                    height: 1.5rem;
                }
            }
        }
    }

    
     .summary-details {
         &:nth-child(1) {
             border-bottom-right-radius: 0;
             border-bottom-left-radius: 0;

             + div {
                border-top: none;
                border-top-right-radius: 0;
                border-top-left-radius: 0;   
                margin-bottom: 2rem;
             }
         }
     }

     &__notes {
        padding: 2rem;
        border-radius: 15px;
        border: 1px solid $border-color;

        label{
            font-size: 1.75rem;
            font-weight: bold;
            color: $theme;
        }
    

        textarea{
            border: none;
            padding: 0;
            background-attachment: local;
            background-image: -webkit-linear-gradient(right, white 0%, white 50%, rgba(255, 255, 255, 0) 51%, rgba(255, 255, 255, 0) 100%), -webkit-linear-gradient(bottom, gray 0%, rgba(128, 128, 128, 0) 8%, rgba(128, 128, 128, 0) 100%);
            line-height: 26px;
            background-size: 8px 26px;
            font-family: 'Noto Sans', sans-serif;
            width: -webkit-fill-available;
            height: 15em;
            resize: none;
            overflow-y: auto;
            outline: none;
        }
     }

     &__footer{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 2rem;
     }


     &__export-modal {
        padding-bottom: 5rem;
        
         h1 {
            font-size: 2.25rem;
            margin-left: 6rem;
            margin-bottom: 3rem;
         }
         .buttons {
            @extend .flex-center;
            
            .button{
                font-weight: normal;

                &:not(:last-child){
                    margin-right: 1rem;
                }

                &:first-child {
                    + button {
                        img {
                            width: 2.85rem;
                        }
                    }
                }
            }            
            
         }
     }
}

