.measure-step-navigation{
    position: relative;
    z-index: 2;

    position: relative;
    z-index: 2;
    height: 100%;
    display: flex;
    flex-flow: column;
    width: 20rem;

    &__step {
        flex: 1;
        position: relative;
        padding-left: 3rem;
        display: flex;
        flex-flow: column;
        align-items: flex-start;
        justify-content: center;
        color: $theme;

        &--active {
            &:after{
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: .5rem;
                background-color: $theme;
                border-top-right-radius: 10px;
                border-bottom-right-radius: 10px;
                box-shadow: 0px 0 11px 0 $theme;
            }

            label,p {
                display: block;;
            }
        }


        &-label {
            display: none;
            font-weight: bold;
            margin-bottom: 1rem;
            font-size: 1.25rem;
        }

        &-description {
            display: none;
            font-size: 2rem;
        }

    }
}