.choice-list {
    width: 100%;

    &__item {
        height: 6rem;
        border: 1px solid $border-color-light-blue;
        border-radius: 8px;
        margin-bottom: 2rem;
        transition: all .25s ease-in-out;
        position: relative;

        display: flex;
        align-items: center;
        padding: 0 2rem;
        font-size: 1.35rem;
        font-weight: bold;

        &::after{
            content: '';
            position: absolute;
            width: 1rem;
            height: 1rem;
            background: #fff;
            right: 2rem;
            bottom: 0;
            top: 0;
            margin: auto;
            border-radius: 50%;
            box-shadow: 0 0 0px 2px $border-color-light-blue;
            transition: all .25s ease;
        }

        &--selected {
            background-color: $theme;
            color: #fff;
            box-shadow: 0 3px 10px 1px rgba($theme, 0.5);

            &.choice-list__item{
                &::after {
                    width: 1rem;
                    height: 1rem;
                    box-shadow: 0 0 0px 2px #fff;
                    border: 2px solid $theme;
                }
            }
        }
    }
}