@import './measures-container';
@import './measure-step-navigation';
@import './measure-modal';
@import './measures-data';
@import './measures-data-card';

.measures {
    height: $pageHeight;

    .measures-step {
        &__form {

            display: grid;
            grid-template-rows: auto;
            grid-auto-rows: auto;
            grid-gap: 1rem;
            align-items: center;
            padding: 3rem 0;

            label {
                color: $theme;
                font-weight: bold;
                font-size: 1.35rem;        
            }
        
            input{
                width: 100%; 
            }

            
            &--one {
                grid-template-columns: 5rem 1fr 1fr;
                > * {
                    flex:1;                    
                    &:first-child + input {
                        grid-column: 2 / -1;
                    }            
                    &:last-child {
                        grid-column: 2 / -1;
                    }
                }
            }

            &--two {
                grid-template-columns: 1fr 1fr;
                grid-template-rows: 3rem;

                label {
                    padding-left: .5rem;
                    font-weight: normal;

                    &:first-child, &:first-child + label {
                        align-self: flex-start;
                        font-weight: bold;
                    }
                }
            }

            &--three {
                grid-template-columns: 10rem 1fr;
            }
        }

        .button {
            margin-left: auto;
        }
    }
}

