.lens-proposal-experience {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 20rem;
    background: $background-dark;

    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding: 3rem;

    .button {
        margin: 0 1rem;
    }

    .icon {
        width: .75rem;
        height: .75rem;
        border: 1px solid #fff;
        border-radius: 50%;
        margin-left: 2rem;
        transform: translateY(.2rem);

        &--active{
            background: $theme;
            border-color: $theme;
        }
    }
}