.my-companion-list{
    flex:1;
    height: calc(100vh - 24rem);
    overflow-y: auto;

    &__price {
        font-size: 1.35rem;
        border: 1px solid;
        padding: .5rem;
        border-radius: 2rem;
        min-width: 8rem;
        display: inline-block;
        text-align: center;
        color: $theme;
    }

    .accordion--child .my-companion-list__price{
        font-size: 1.2rem;
        padding: .4rem;
        min-width: 7rem;
    }
}