.start-from {
    width: 100%;
    background: #fff;
    padding: 2rem;
    border-radius: 1.5rem;
    background-color: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(10px);

    @extend .flex-center;

    label{
        margin-right: auto;
        font-size: 1.5rem;
        font-weight: bold;
    }

    > * {
        &:last-child{
            margin-left: 1rem;
        }
    }
}