.need-explorer-question-panel {
    @extend .box-shadow-light-blue;
    padding: 4rem 5.5rem;
    border-radius: 15px;

    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-flow: column;


    &__step {
        font-size: 1.2rem;
        color: $theme;
        font-weight: bold;

        display: flex;
        align-items: center;

        img{
            margin-right: 2rem;
        }
    }

    &__next {
        margin-left: auto;
        margin-top: auto;
    }

}