.lens-consultation-type{
    flex: 1;
    padding: 5rem 0;

    &__list {
        height: 100%;
        display: flex;
        flex-flow: column;
        justify-content: space-between;

        &-item{
            display: flex;
            justify-content: space-between;
            font-size: 2.25rem;
            color: $theme;
            font-weight: bold;
            padding: 0 3rem;

            &:not(:last-child):not(:first-child) {
                border-bottom: 1px solid $border-color-light;
                padding-bottom: 5rem;
            }

            button {
                width: 4.5rem;
                height: 4.5rem;
            }

            &:first-child {
                border: 1px solid $border-color-light-blue;                
                height: 7rem;
                display: flex;
                align-items: center;
                border-radius: 35px;
                font-weight: normal;
                font-size: 1.75rem;
            }

            &-value {
                background: rgba($color: $theme, $alpha: 0.1);
                height: 6rem;
                border-radius: 12px;
                padding: 0 3rem;
                display: flex;
                align-items: center;
                justify-content: space-between;
                color: $text-color-main;
                font-size: 1.85rem;
            }

            &--with-value {
                font-weight: normal;
                flex-flow: column;
                padding-left: 0;
                padding-right: 0;

                label {
                    font-size: 1.5rem;
                    padding: 0rem 0 1rem 3rem;
                }
            }
        }
    }
}