.my-companion-footer{
    border-top: 1px solid $border-color-light;
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    height: 8rem;

    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 0 3rem;
    background:#fff;

    .button {
        &:first-child{
            margin-right: 1rem;
        }

        &:last-child{
            margin-left: auto;
        }
    }

    &__saved {
        font-size: 1.25rem;
        color: $theme;

        height: 100%;
        flex-flow: column;
        @extend .flex-center;

        h1{
            margin-top: 2rem;
        }
    }
}