.side-box-bordered {
    height: 100%;
    width: 39rem;
    padding: 2.5rem;
    border-radius: 15px;
    margin-right: 4rem;
    position: relative;
    

    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-flow: column;

    @extend .box-shadow-light-blue;


    &:before {
        content: '';
        width: .65rem;
        height: 100%;
        background-color: $theme;
        position:absolute;
        left: 0;
        top: 0;
        border-top-right-radius: .5rem;
        border-bottom-right-radius: .5rem;
        @extend .box-shadow-light-blue;
    }
}