.App {
    width: 100vw;
    height: 100vh;
    // max-height: 800px;
    max-width: 1440px;
    margin: auto;
    background-color: #fff;
    margin: auto;
}


.page {
    height: $pageHeight;
    padding: 2rem 3.5rem 3rem;
}

/* width */
::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: rgba(90, 102, 117, 0.1);
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: $theme;
    border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: $theme;
}