.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2rem 3.5rem;
    position: relative;

    &__logo{
        width:4.4rem;
        height: 4.4rem;

        img{
            height: 100%;
            width: 100%;
        }
    }

    &__title{
        font-family: 'AkhandSoft-Bold', Arial, sans-serif;
        color: $text-color-main;
        font-size: 2.5rem;
        @extend .position-center-absolute;
    }

    &__navigation {
        @extend .flex-center;

        > *:first-child:not(:last-child) {
            margin-right: 2rem;
        }

        .spacer {
            width: 2px;
            height: 1rem;
            background-color: #eee;
            margin: 0 2.5rem;
        }
    }
}