.need-explorer-question {
    width: 100%;
    display: flex;
    flex-flow: column;
    flex:1;
    padding: 2rem 0;

    &__question {
        font-size: 2.5rem;
        margin: 0rem 0 3rem;

        &--with-subquestion{
            margin-bottom: 1rem;
        }
    }

    &__sub-question {
        font-size: 1.5rem;
        color: $text-color-light;
        margin-bottom: 2rem;
    }
}