.summary-details {
    border: 1px solid $border-color;
    padding: 2rem;
    border-radius: 15px;
    position: relative;
    color: $text-color-sub;

    &__data {
        flex:1;
    }

    &__wrapper {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: flex-start;
    }

    &__label {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-bottom: 3rem;

        label{
            color: $theme;
            font-size: 1.5rem;
            font-weight: bold;
            margin-right: auto;
        }

        span {
            background: $theme-secondary;
            padding: .5rem 2rem;
            border-radius: 15px;
            font-weight: bold;
            color: $theme;
        }
    }

    &__lens{
        display: flex;
        color: $text-color-sub;

        &-type {
            display: flex;
            align-items: flex-start;
            margin-right: 3rem;
            width: 13rem;

            img{
                width: 4rem;
                margin-right: .5rem;
            }
    
            h2{
                display: flex;
                flex-flow: column;
                text-transform: uppercase;
                line-height: 1.5;
                font-size: 1.35rem;
    
                span {
                    font-size: 1.2rem;
                    font-weight: normal;
                    text-transform: lowercase;
                }
            }
        }

        &-products {
            padding-bottom: 5rem;
            // width: 26rem;
            flex: 1;
            position: relative;
    
            &::after{
                content: "";
                width: 1px;
                height: 100%;
                position: absolute;
                right: -2.5rem;
                top: 1.9rem;
                background: $text-color-sub;
            }
        }

        &-product {
            padding-bottom: 2rem;
    
            > label {
                background: $theme-secondary;
                padding: 1rem;
                font-size: 1.2rem;
                width: 100%;
                display: block;
                text-align: center;
                position: relative;
    
                &::before, &::after{
                    content: "";
                    width: 2rem;
                    height: 1px;
                    background: $text-color-sub;
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                }
    
                &::after {
                    left: -2.5rem;
                }
    
                &::before {
                    right: -2.5rem;
                }
            }

            > ul {
                display: flex;
                justify-content: space-between;
        
                li {
                    display: flex;
                    flex-flow: column;
                    align-items: center;
                    
                    img{
                        width: 5rem;
                    }
        
                    label{
                        width: 6rem;
                        font-size: .8rem;
                        text-align: center;
                    }
                }
            }
        }
    }

    &__lens-image {
        transform: translate(1.5rem, -2.5rem);        
        height: 22rem;
    }

    &__lens-thickness {
        display: flex;
        align-items: center;
        padding: 3rem 0;
        
        img {
            width: 3rem;
            margin-right: 1rem;
        }

        label {
            font-size: 1.5rem;
            font-weight: bold;
        }

        span {
            background-color: $theme-secondary;
            height: 3.5rem;
            width: 3.5rem;
            margin-left: 1rem;
            @extend .flex-center;

        }
    }
}

.summary-details {
    &__lens {

        &:first-child,
        &:last-child {
            .summary-details__lens-product {
                label {
                    &::after {                
                        width: 3rem;
                        left: -3.5rem;
                    }
                }
            }
        }

        &:first-child {
            &+div {
                .summary-details__lens-type {
                    height: 5rem;
                    margin-top: auto;
                    margin-bottom: auto;
                    transform: translateY(-8rem);
                    position: relative;

                    &::after{
                        content: "";
                        position: absolute;
                        right: -5px;
                        top: 0;
                        width: 1.5rem;
                        height: 1px;
                        background-color: $text-color-sub;
                        bottom: 0;
                        margin: auto;
                    }
                }

                .summary-details__lens-product{
                    position: relative;

                    &:first-child{
                        &::after{
                            content: "";
                            position: absolute;
                            width: 1px;
                            height: 100%;
                            background-color: $text-color-sub;
                            top: 1.9rem;
                            left: -2.5rem;
                        }

                        &::before{
                            content: "";
                            position: absolute;
                            width: 5rem;
                            height: 1px;
                            background-color: $text-color-sub;
                            top: 0;
                            bottom: 0;
                            right: -7.5rem;
                            margin: auto;
                            transform: translateY(2rem);
                        }
                    }                
                }
            }
        }

        &:last-child{
            .summary-details__lens-products{
                padding-bottom: 0;
                &::after{
                    display: none;
                }
            }
            .summary-details__lens-product{
                padding-bottom: 0;
            }
        }
    }
}