.lens-consultation-product{
    flex: 1;

    .button {
        border-color: $border-color-light;
        margin-bottom: 4rem;
    }

    &__list {
        &-item{
            > div {
                height: 5.7rem;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0 3rem;
                font-size: 1.35rem;
                border-top: 1px solid $border-color-light;
                border-bottom: 1px solid $border-color-light;
                position: relative;
                transition: all .25s ease-in;    
            }

            &--selected {
                > div {
                    color: $theme;
                    background: rgba($theme, 0.20);

                    > img {
                        transform: rotate(180deg);
                    }

                    &::after {
                        content: '';
                        width: .5rem;
                        height: 100%;
                        background: $theme;
                        position: absolute;
                        top: 0;
                        left: 0;
                        border-top-right-radius: 3px;
                        border-bottom-right-radius: 3px;
                    }
                }
            }

            > img {
                transition: all .25s ease-in;
            }
        }
    }


    &__sub-list {
        margin-left: 5rem;

        &-item {
            height: 5rem;
            border-bottom: 1px solid $border-color-light;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 3rem;
            position: relative;
            font-size: 1.25rem;

            &--selected {
                color: $theme;

                &::after {
                    content: '';
                    width: .5rem;
                    height: 100%;
                    background: $theme;
                    position: absolute;
                    top: 0;
                    left: 0;
                    border-top-right-radius: 3px;
                    border-bottom-right-radius: 3px;
                }
            }
        }
    }
}