@import './start-from';
@import './age-category';
@import './continue-with-id';

.home {
    display:grid;
    width: 100%;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 29rem 1fr;
    grid-gap: 1.5rem;


    &__card {
        border: 2px solid $border-color-light-blue;
        border-radius: 15px;

        @extend .flex-center;
        flex-flow: column;

        label{
            font-size: 2.5rem;
            font-weight: bold;
            margin-top:1.5rem;
        }
    }

    &__main {
        grid-column: 2 / -1;
        grid-row: 1 / -1;
        border-radius: 15px;
        background-image: url('../../../assets/images/home-content-background.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: top center;

        color: #fff;
        padding: 3rem;

        @extend .flex-center;
        flex-flow: column;
        position: relative;
    }

    &__content {
        flex:1;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-flow: column;
    }

    &__eyeglass{
        width: 7rem;
        margin-top: 8rem;
    }

    &__banner{
        font-size: 4rem;
        max-width: 42rem;
        width: 100%;
        line-height: 1.2;
        text-align: center;
        margin-bottom: 5rem;
    }

    &__start-from-id-popup{
        padding: 1rem 5rem 5rem;

        h1 {
            font-size: 2.5rem;
            margin-bottom: 4rem;
        }

        div {
            display: flex;
            position: relative;

            input {
                flex: 1;
            }

            button{
                position: absolute;
                right: 0;
                height: 100%;
            }
        }
    }
}