.lens-consultation-brand{
    flex: 1;
    
    .button {
        border-color: $border-color-light;
        margin-bottom: 4rem;
    }

    &__list {
        li {
            height: 6rem;
            font-size: 1.75rem;
            padding: 0 3rem;

            display: flex;
            align-items: center;
            justify-content: space-between;
            border: 1px solid $border-color-light-blue;
            border-radius: 12px;
            margin-bottom: 1.5rem;
        }
    }

    
}