.measure-modal {
    position: absolute;
    top: 3.5rem;
    right: 3.5rem;
    z-index: 2;
    width: 35rem;
    border-radius: 12px;
    padding: 2rem;
    background-color: #fff;

    &__head{
        font-size: 1.25rem;
        font-weight: bold;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__label {
        font-size: 1.35rem;
        font-weight: bold;
        margin-bottom: 1rem;
        display: block;
    }

    &__description {
        font-size: 1.35rem;
        color: $text-color-light;
        line-height: 1.5;
    }
}