.prescription-form{

    &__precision {
        width: 36rem;
        padding: 1rem;
        background-color: rgba($theme, 0.06);
        margin: 4rem auto;

        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 8px;

        button {
            height: 4rem;
            padding: 0 2rem;
            color: $theme;
            background: transparent;
            border: none;
            font-size: 1.5rem;
            border-radius: 6px;
            outline: none;

            &.active {
                background: #fff;
                font-weight: bold;
                box-shadow: 0 2px 7px -2px rgba($theme, 0.35);
            }
        }
    }

    &__field {
        @extend .flex-center;
        flex-wrap: wrap;
        margin-bottom: 2rem;

        > label {
            width: 100%;
            color: $theme;
            font-size: 1.2rem;
            padding: 1rem 3rem;

            & + div + div{
                margin-left: 2rem;
            }
        }

        .prescription-input{
            flex: 1;
        }
     }
}