.need-explorer-finished {
    height: 100%;

    @extend .flex-center;

    &__modal {
        height: 33rem;
        width: 51rem;
        border-radius: 15px;
        @extend .box-shadow-light-blue;

        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-flow: column;
        padding: 6rem;

        &-text {
            font-size: 2.5rem;
            color: $theme;
        }
    }

    &__modify{
        text-decoration: underline;
        color: $text-color-main;
        font-size: 1.2rem;
    }
}