.prescription-side {

    &__title{
        display: flex;
        align-items: center;
        color: $theme;
        font-size: 1.3rem;

        img{
            margin-right: 1rem;
        }
    }


    &__image {
        margin: auto;
        width: 80%;
    }


    &__buttons {
        display: flex;
        margin-left: auto;

        .button {
            margin-left: 1rem;
        }
    }

    &__import-modal {
        height: 100%;
        display: flex;
        align-items: flex-start;
        flex-flow: column;
        padding: 1rem 4rem;

        h1 {
            font-size: 2.25rem;
        }

        div {
            display: flex;
            align-items: center;
            justify-content: center;
            margin: auto;

            .button {
                margin: 0 .5rem;
            }
        }
    }
}