.lens-thickness-control {
    flex: 1;

    &__prescription {
        background: $theme;
        border-radius: 15px;
        height: 8rem;
        color: #fff;
        display: flex;
        align-items: center;
        padding: 0 4rem;
        font-size: 2rem;
        margin-bottom: 2rem;
        
        span {

            font-size: 1.35rem;
            color: #fff;
            padding: 0.75rem 3rem;
            background-color: #5ebeb3;
            border-radius: 3rem;
            font-weight: bold;
            margin-left: 2rem;
            position: relative;

            &::before {
                content: '';
                width: .5rem;
                height: .5rem;
                background: #fff;
                border-radius: 50%;
                position: absolute;
                top: 0;
                left: 1.5rem;
                bottom: 0;
                margin: auto;
            }
        }

        img {
            margin-left: auto;
            width: 3.5rem;
            height: 3.5rem;
        }
    }

    &__controls {
        border-radius: 12px;
        border: 1px solid $border-color-light-blue;
        overflow: hidden;

        &-head {
            height: 6rem;
            padding:  0 0rem 0 3rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 1.75rem;
            overflow: hidden;
            border-bottom: 1px solid $border-color-light-blue;

            span {
                height: 100%;
                padding: 0 3rem;
                background: $theme-secondary;
                color: $theme;

                @extend .flex-center;
            }
        }

        &-instruction {
            font-size: 1.5rem;
            color: $text-color-light;
            padding: 1rem 2rem;
            display: inline-block;
        }

        .button {
            margin: 1rem 2rem 3rem;
        }
        

        &-half-circles {
            height: 15rem;
            display: flex;
            align-items: flex-start;
            justify-content: center;
        }

        &-slider{
            padding: 3rem 2rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: relative;


            > div {
                // background: $theme;
                height: 1px;
                flex: 1;
                margin: 0 2.5rem;

                > div {
                    &:last-child {
                        div{
                            background: $theme;
                        }
                    }
                }
            }

            &-index {
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: space-between;
                width: calc(100% - 13rem);
                pointer-events: none;
                left: 0;
                margin: auto;
                right: 0;


                span {
                    width: 1px;;
                    height: 1rem;
                    background: $theme;
                    display: inline-block;

                    &:first-child,
                    &:last-child{
                        background: transparent;
                    }
                }
            }
        }
    }
}