.summary-side {
    width: 30.5rem;
    border-radius: 15px;
    padding: 3rem;
    border: 1px solid $theme;
    height: 100%;    
    display: flex;
    flex-flow: column;
    margin-right: 3rem;
    position: relative;
    overflow: hidden;

    &__lens-pairs {
        display: flex;
        align-items: center;
        justify-content: center;

        span {
            width: 1px;
            height: 2rem;
            background: #eee;
            margin: 0 2rem;
            display: inline-block;
        }
    }

    &__lens {
        position: relative;
        flex:1;

        img {
            position: absolute;
            left: -4rem;
            top: 0;
            bottom: 0;
            margin: auto;
            height: 90%;

            &:first-child{
                transform: translateX(-3rem);

                &+img{
                    opacity: .7;
                }
            }

            &:last-child{
                transform: translateX(+3rem);
                opacity: .3;
            }
        }
    }

    &__price{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 6.7rem;
        font-size: 1.75rem;
        color: #fff;
        background-color: $theme;

        @extend .flex-center;
    }
}