.lens-proposal-builder-footer {
    grid-row: 4 / -1;
    grid-column: 1 / -1;

    display: flex;
    align-items: center;
    justify-content: space-between;

    &__id, &__experiences{
        display: flex;
        align-items: center;

        img {
            margin-left: 2rem;
        }
    }

    &__experiences {
        position: relative;
        z-index: 6;
    }

    label {
        font-size: 1.4rem;
        font-weight: bold;
    }
}