.package-offer {
    display: flex;
    flex-flow: column;
    align-items: center;

    &__buttons{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 5rem;
        width: 100%;
    }

    &__packages {
        flex: 1;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr;
        grid-gap: 2rem;

        width: 100%;
        max-width: 96rem;
    }

    &__package {
        border: 1px solid $border-color-light-blue;
        border-radius: 15px;

        @extend .flex-center;
        font-size: 2rem;
        font-weight: bold;
    }
}