.need-explorer-progress {
    padding: 1.5rem;
    background: $theme;
    border-radius: 8px;
    color: #fff;
    font-size: 1.25rem;
    
    &__text {
        display: flex;
        align-items: center;
        justify-content: space-between;

        label:first-child {
            font-weight: bold;
        }
    }

    &__bar {
        background-color: darken($color: $theme, $amount: 5);
        width: 100%;
        height: .65rem;
        margin-top: .8rem;
        border-radius: 6px;
        overflow: hidden;
        position: relative;

        span{
            transition: width .25s ease-in-out;
            width: 0%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background-color: #fff;
            border-radius: 6px;
        }
    }
}