.lens-proposal-builder-card{
    border-radius: 15px;
    border: 1px solid $border-color-light-blue;
    padding: 3rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    overflow: hidden;

    &--completed {
        background-color: $theme;
        box-shadow: 0 6px 13px 0px rgba($color: $theme, $alpha: 0.42);

        h2 {
            color: #fff;
        }
    }

    &--with-footer{
        h2 {
            transform: translateY(-2rem);
        }
        .button {
            transform: translateY(-2rem);
        }
    }

    &__info{
        h2 {
            font-size: 2.5rem;
        }

        span {
            color: #fff;
            display: inline-block;
            padding: .25rem 2.5rem;
            background: #5ebeb3;
            border-radius: 12px;
            font-size: 1.2rem;
            margin-top: 2.5rem;
            position: relative;

            &::after{
                content: '';
                width: .5rem;
                height: .5rem;
                background-color: #fff;
                display: inline-block;
                position: absolute;
                left: 1.25rem;
                top: 0;
                bottom: 0;
                margin: auto;
                border-radius: 50%;
            }
        }
    }
    button {
        width: 5rem;
        height: 5rem;    
        @extend .flex-center;    
    }
}