.flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.position-center {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.position-center-fixed {
    position:fixed;
    @extend .position-center;
}

.position-center-absolute {
    position:absolute;
    @extend .position-center;
}

.box-shadow-light-blue {
    box-shadow: 0px 0px 60px -15px rgba($theme, 0.32);
}