.button {
    border: none;
    padding: 1.2rem 1.75rem;
    border-radius: .7rem;
    font-size: 1.5rem;
    font-weight: bold;
    transition: all .25s ease-in-out;
    height: 4rem;

    display: flex;
    align-items: center;
    justify-content: space-between;
    outline: none;

    // BUTTON TYPES
    &--primary {
        background-color: $theme;
        color: #fff;
        border: 1px solid $theme;
    }

    &--secondary{
        background-color: #fff;
        color: $theme;
    }

    &--simple{
        background-color: transparent;
        border-color: #fff;
        color: #fff;

        &.button{
            &--active {
                background-color: #fff;
                color: $text-color-main;
            }
        }
    }
    // BUTTON TYPES END

    &--icon-only {
        height: 4rem;
        width: 4rem;
        padding: 0;
        display: flex;
        justify-content: center;
        
        .button__icon {
            margin-left: 0;
        }   
    }

    &--outlined {
        border: 1px solid $theme;
    }

    &--circle {
        border-radius: 50%;
    }

    &__icon{
        margin-left: 1rem;
    }

    &--icon-left {
        flex-flow: row-reverse;

        img{
            margin-right: 1rem;
            margin-left: initial;
        }
    }
}