.age-category {
    padding: 2rem;
    background-color: #fff;
    border-radius: 1.5rem;
    background-color: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(10px);

    width: 100%;


    &__head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 2rem;
    }

    &__selection {
        
        display: grid;
        grid-template-rows: 1fr;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 2rem;

    }

    &__choice { 
        background: #fff;
        border-radius: 1.5rem;
        height: 13.5rem;

        @extend .flex-center;
        color: $theme;
        font-size: 1.5rem;
        font-weight: bold;
        cursor: pointer;

        label{
            width: 8rem;
            text-align: center;
        }

        &--active, &:hover{
            background: $theme;
            color: #fff;
        }
    }
}