.breadcrumb{
    padding: 0rem 3.5rem 2.25rem;

    ul {
        li {
            display: inline-block;

            &:not(:first-child) {
                margin-left: 2rem;
            }
        }
    }

    &__home {
        width: 1.5rem;
        height: 1.2rem;
        transform: translateY(2px);
    }

    &__item {
        &:not(:first-child) {
            a {
                margin-left: 2rem;
            }
        }

        a {
            font-size: 1.25rem;
            color: $text-color-light;
            position: relative;
        }
        

        &:last-child{
            a{
                color: $theme;
                font-weight: bold;

                &::after {
                    content: '';
                    width: 5px;
                    height: 5px;
                    background:$theme;
                    position: absolute;
                    bottom: -8px;
                    left: 0;
                    right: 0;
                    margin: auto;
                    border-radius: 50%;
                }
            }
        }
    }
}