.lens-consultation-side{

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        
        label {
            font-weight: bold;
            font-size: 1.3rem;
            color: $theme;
            display: flex;
            align-items: center;

            img{
                margin-right: 1rem;
            }
        }    
    }

    &__lens-pairs{
        display: flex;
        align-items: center;

        span {
            width: 1px;
            height: 2rem;
            background: #eee;
            margin: 0 2rem;
            display: inline-block;
        }
    }

    &__lens {
        position: relative;
        width: 100%;
        flex: 1;

        img{
            position: absolute;
            top:0;
            left: 0;
            bottom: 0;
            margin: auto;
            height: 90%;

            &:first-child {
                transform: translateX(-10px);
                z-index: 5;

                &+img{
                    opacity: 0.7;
                    transform: translateX(+20px);
                    z-index: 3;
                }
            }
            &:last-child {
                transform: translateX(+50px);
                opacity: 0.3;
                z-index: 1;
            }

            &.transition {
                transform: translateX(20px);
                z-index: 4;
            }
        }

        &--with-lens-type{
            img {
                opacity: 1 !important;
            }
        }
    }

    &__buttons {
        display: flex;
        justify-content: flex-end;
        width: 100%;

        .button {
            margin-left: 1rem;
        }
    }


    &__slider {
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-flow: column;
        z-index: 10;

        > div {
                width: 2.8rem;
                background: rgba($theme, 0.15);
                border-radius: 30px;
                margin: 2rem 0;


                > div {
                    &:last-child{
                        div {
                            background: $theme;
                        }
                    }
                }            
        }
    }

}