.menu {
    height: 100vh;
    background: #fff;
    width: 22rem;
    display: flex;
    flex-flow: column;

    &--disabled {
        background-color: rgb(216,224,234);

        .menu__item {
            opacity: 0.2;
        }
    }


    &__title{
        color: $theme;
        font-size: 1.5rem;
        font-weight: bold;
        height: 9rem;
        @extend .flex-center;

        border-bottom: 2px solid $border-color-light;
    }

    &__items{
        flex: 1;
        display: flex;
        align-items: stretch;
        justify-content: center;
        flex-flow: column;
    }

    &__item{
        flex:1;
        @extend .flex-center;
        flex-flow: column;

        label {
            font-size: 1.5rem;
            font-weight: bold;
            padding: 1.5rem 0;
        }

        &--active {
            background-color: $theme;
            opacity: 1 !important;
            color: #fff;
        }
    }
}