.slider-group{
    width: 100%;

    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 7rem;
    grid-auto-rows: 7rem;
    grid-gap: 2rem 4.5rem;

    &__slider-label{
        font-size: 1.2rem;
        margin-bottom: 1rem;
        display: inline-block;
    }

    &__slider {
        > div {
            width: 100%;
            height: 2rem;
            border-radius: 10px;
        }
    }
}

