.need-explorer-anamnesis-details {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;

    &__side {
        width: 25rem;
        background-color: $theme;
        padding: 3rem;

        &-tag{
            background: #fff;
            color: $theme;
            padding: .25rem 1rem;
            border-radius: 2.5px;
        }

        &-title {
            color: #fff;
            font-size: 1.5rem;
            margin: 2rem 0;
        }

        &-description {
            color:#fff;
            font-size: 1.5rem;
            line-height: 1.5;
        }
    }

    &__details {
        flex: 1;
        background: #fff;
        padding-right: 3.5rem;
        position: relative;
        z-index: 1;
    }

    &__scroll{
        overflow-y: auto;
        padding-right: 5rem;
        height: 100%;
    }

    &__summary {
        padding: 2rem 3rem 1rem;
        border-bottom: 1px solid $border-color-light;

        &-question {
            font-size: 1.5rem;
            margin-bottom: 1.5rem;
        }

        &-answer {
            padding: .5rem 1.25rem;
            border-radius: 15px;
            color: $theme;
            border: 1px solid $theme;
            font-size: 1.1rem;
            display: inline-block;
            margin-right: 1rem;
            margin-bottom: .5rem;

        }
    }

    &__close {
        position: absolute;
        top: 17px;
        right: 15px;
        z-index: 5;
        background: #fff;
        border-radius: 50%;
        box-shadow: 0 0 0 20px #fff;
    }
}