.need-explorer-anamnesis {
    height: 100%;
    display: flex;

    &__left {
        width:38rem;
        height:45rem;
        border-radius: 15px;
        margin-right: 4rem;

        @extend .flex-center;
        flex-flow: column;
        @extend .box-shadow-light-blue;

    }

    &__modify{
        text-decoration: underline;
        color: $text-color-main;
        font-size: 1.2rem;
    }
    
    &__improve {
        margin: 8rem 0 3rem;
    }

    &__cards {
        flex:1;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 18rem;
        grid-auto-rows: 18rem;
        grid-gap: 2rem;

    }

    &__card {
        border:1px solid $border-color-light-blue;
        border-radius: 8px;
        padding: 2rem;

        display: flex;
        align-items: flex-start;
        flex-flow: column;

        &-tag{
            background-color: $theme;
            color: #fff;
            font-size: 1.25rem;
            padding: .25rem 1rem;
            border-radius: 3px;
            margin-bottom: 1rem;
        }

        &-text {
            font-size: 1.65rem;
            font-weight: bold;
        }
         
        &-icon{
            // margin-top: auto;
            width: 2.55rem;
            height: 2.55rem;
            margin-top: auto;
            margin-left: auto;
        }
    }
}