.prescription-input {
     border: 1px solid $border-color-light-blue;
     border-radius: 8px;
     display: flex;
     height: 5rem;
     font-size: 1.75rem;
     overflow: hidden;

     &__label {
        width: 8rem;
        display: flex;
        @extend .flex-center;
        background: rgba($theme, 0.15);
     }

     &__value {
        flex:1;
        @extend .flex-center;
     }
}