.need-explorer-category {
    padding-top: 4rem;

    &__item {
        @extend .flex-center;
        margin-bottom: 2rem;

        label {
            height: 3rem;
            border-radius: 15px;
            // background: #eee;
            @extend .flex-center;
            font-size: 1.5rem;
            padding: 0 3rem;
            color: $text-color-light;
        }

        &--active {
            label {
                background: $theme;
                color: #fff;
            }
        }
    }
    
}