.drag-and-drop-sorter{
    &__drag {
        display: flex;

        &-item {
            flex: 1 ;
            border: 1px solid $theme;
            border-radius: 8px;
            padding:2rem;
            height:15rem;
            @extend .flex-center;
            font-size: 1.2rem;
            color: $theme;

            &:not(:last-child){
                margin-right: 1rem;
            }
        }
    }

    &__drop {
        display: flex;
        margin-bottom: 2rem;

        &-item {   
            flex: 1;
            border: 1px solid $border-color-light-blue;
            border-radius: 8px;
            padding:2rem;
            height:15rem;
            font-size: 2.5rem;
            font-weight: bold;
            color: $theme;

            @extend .flex-center;

            &:not(:last-child){
                margin-right: 1rem;
            }
        }
    }
}

