.choice-box-image {
    width: 100%;


    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 14.5rem;
    grid-gap: 2rem;
    grid-auto-rows: 14.5rem;


    &__item {
        height: 100%;
        border: 1px solid $border-color-light-blue;
        border-radius: 8px;
        margin-bottom: 2rem;
        transition: all .25s ease-in-out;
        position: relative;

        display: flex;
        flex-flow: column;
        justify-content: space-between;
        padding: 1rem 1rem 1.5rem 1.5rem;

        font-size: 1.35rem;
        font-weight: bold;
        color: #fff;
        overflow: hidden;

        label{
            position: relative;
        }

        &--selected {
            box-shadow: 0 0px 0px 4px #fff, 0 0px 7px 3px #0000006e;

            .choice-box-image__check{
               background: #fff;

               &::after{
                    opacity: 1;
               }
            }
        }
    }

    &__image {
        // @extend .position-center-absolute;
        position:absolute;
        top:0;
        left: 0%;
        width: 100%;
        height: 100%;
        background-position: top center;
        background-size: cover;
        
    }

    &__check{
        position: relative;
        display: block;
        width: 1.75rem;
        height: 1.75rem;
        border-radius: 50%;
        // 
        background: rgba(255, 255, 255, 0.3);
        margin-left: auto;

        &::after{
            opacity: 0;
            content: "";
            position: absolute;
            height: 4px;
            width: 7px;
            border-left: 1px solid #0386D6;
            border-bottom: 1px solid #0386D6;
            top: 45%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(-45deg);
        }
    }
}