.modal{
    overflow: hidden;
    display: flex;
    flex-flow: column;

    &-overlay{
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100vw;
        background: rgba(0, 0, 0, 0.5);
        z-index: 9000;
    }

    @extend .position-center-fixed;
    background: #fff;
    padding: 1.75rem;
    z-index: 9001;
    border-radius: 2rem;
    //default
    width:54.5rem;
    max-width: calc(100vw - 5rem);

    &--transparent {
        background-color: transparent;
    }

    &__head {
        display: flex;
        position: relative;
        z-index: 2;
    }

    &__close {
        margin-left: auto;
    }

    &__content {
        height: 100%;
        flex: 1;
    }
}